define('dummy/controllers/application', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({

    //Bootstrap Styling
    items1: Ember['default'].computed(function () {
      var store = this.store;
      return [{
        content: function content(parent) {
          return store.findAll('manager');
        },
        extras: {
          label: 'Manager',
          width: 'col-xs-2'
        }
      }, {
        content: function content(parent) {
          return store.findAll('sub-manager');
        },
        extras: {
          label: 'Sub Manager',
          width: 'col-xs-2'
        }
      }, {
        content: function content(parent) {
          return store.findAll('employee');
        },
        extras: {
          label: 'Employee',
          width: 'col-xs-2'
        }
      }, {
        content: function content(parent) {
          return store.findAll('task');
        },
        extras: {
          label: 'Task',
          width: 'col-xs-2'
        }
      }, {
        content: function content(parent) {
          return store.findAll('sub-task');
        },
        extras: {
          label: 'Sub Task',
          width: 'col-xs-2'
        }
      }];
    }),

    items1Selection: null,

    //Select X
    items2: Ember['default'].computed(function () {
      var store = this.store;
      return [{
        content: function content(parent) {
          return store.findAll('manager');
        },
        extras: {
          label: 'Manager',
          width: 'col-xs-2'
        },
        selection: store.find('manager', 1)
      }, {
        content: function content(parent) {
          return store.findAll('sub-manager');
        },
        extras: {
          label: 'Sub Manager',
          width: 'col-xs-2'
        }
      }, {
        content: function content(parent) {
          return store.findAll('employee');
        },
        extras: {
          label: 'Employee',
          width: 'col-xs-2'
        }
      }, {
        content: function content(parent) {
          return store.findAll('task');
        },
        extras: {
          label: 'Task',
          width: 'col-xs-2'
        }
      }, {
        content: function content(parent) {
          return store.findAll('sub-task');
        },
        extras: {
          label: 'Sub Task',
          width: 'col-xs-2'
        }
      }];
    }),

    items2Selection: null,

    //Preselected items
    items3: [],

    //Bootstrap Styling
    items3Update: Ember['default'].on('init', function () {
      var _this = this;

      var store = this.store;
      Ember['default'].RSVP.all([store.findRecord('manager', 1), store.findRecord('sub-manager', 2), store.findRecord('employee', 3), store.findRecord('task', 4), store.findRecord('sub-task', 5)]).then(function (results) {
        var items = [{
          content: function content(parent) {
            return store.findAll('manager');
          },
          extras: {
            label: 'Manager',
            width: 'col-xs-2'
          },
          selection: results[0]
        }, {
          content: function content(parent) {
            return store.findAll('sub-manager');
          },
          extras: {
            label: 'Sub Manager',
            width: 'col-xs-2'
          },
          selection: results[1]
        }, {
          content: function content(parent) {
            return store.findAll('employee');
          },
          extras: {
            label: 'Employee',
            width: 'col-xs-2'
          },
          selection: results[2]
        }, {
          content: function content(parent) {
            return store.findAll('task');
          },
          extras: {
            label: 'Task',
            width: 'col-xs-2'
          },
          selection: results[3]
        }, {
          content: function content(parent) {
            return store.findAll('sub-task');
          },
          extras: {
            label: 'Sub Task',
            width: 'col-xs-2'
          },
          selection: results[4]
        }];

        _this.set('items3', items);
      });
    }),

    items3Selection: null

  });

});